.spark-loading-wrapper {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  opacity: 0.3;
  background: black;
  z-index: 1111;
}
.spark-loading {
  z-index: 1112;
  width: 150px;
  height: 15px;
  position: fixed;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.spark-loading span {
  display: inline-block;
  width: 15px;
  height: 100%;
  margin-right: 5px;
  background: lightgreen;
  -webkit-transform-origin: right bottom;
      -ms-transform-origin: right bottom;
          transform-origin: right bottom;
  -webkit-animation: load 1s ease infinite;
          animation: load 1s ease infinite;
}
.spark-loading span:last-child {
  margin-right: 0px;
}
@-webkit-keyframes load {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
  }
}
@keyframes load {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
  }
}
.spark-loading span:nth-child(1) {
  -webkit-animation-delay: 0.13s;
          animation-delay: 0.13s;
}
.spark-loading span:nth-child(2) {
  -webkit-animation-delay: 0.26s;
          animation-delay: 0.26s;
}
.spark-loading span:nth-child(3) {
  -webkit-animation-delay: 0.39s;
          animation-delay: 0.39s;
}
.spark-loading span:nth-child(4) {
  -webkit-animation-delay: 0.52s;
          animation-delay: 0.52s;
}
.spark-loading span:nth-child(5) {
  -webkit-animation-delay: 0.65s;
          animation-delay: 0.65s;
}



