.login-dialog .spark-dialog-content {
  width: 350px;
}
.login-dialog .login-dialog-body,
.login-dialog .regist-dialog-body {
  margin: 20px 30px;
}
.login-dialog .spark-dialog-footer {
  text-align: center;
}
.login-dialog input {
  margin: 5px 0;
}
.login-dialog .spark-dialog-body {
  padding: 0;
}
.login-dialog .friendly-tips {
  margin-top: 20px;
  text-align: center;
}
.login-dialog .friendly-tips a {
  color: #1890ff;
  margin-left: 3px;
  text-decoration: none;
}



