.doc-list {
  width: 764px;
  margin: 20px auto;
  height: calc(100% - 180px);
  overflow: auto;
}
.doc-list::-webkit-scrollbar {
  width: 0;
}
.doc-list .create-doc-button {
  text-align: center;
  position: fixed;
  bottom: 40px;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}
.doc-list .doc-item {
  display: inline-block;
  margin: 20px;
}
.doc-list .doc-card {
  background: white;
  padding: 12px 20px;
  border: 1px solid #e5e5e5;
  width: 300px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  cursor: pointer;
}
.doc-list .doc-card:hover {
  -webkit-box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
          box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
}
.doc-list .doc-card .doc-logo {
  background: url(data:image/png;base64,);
  height: 30px;
  width: 30px;
  background-size: contain;
  margin-right: 15px;
}
.doc-list .doc-card .doc-title {
  font-size: 12px;
  line-height: 20px;
  color: #494949;
  margin-bottom: 2.5px;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}
.doc-list .doc-card .doc-time {
  font-size: 12px;
  line-height: 18px;
  color: #a5a5a5;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}



