html,
input,
textarea {
  font-family: 'Roboto', sans-serif;
  line-height: 1.4;
  background: #eee;
}
html,
body,
#root,
.App,
.editor {
  height: 100%;
}
body {
  margin: 0;
}
p {
  margin: 0;
}
pre {
  padding: 10px;
  background-color: #eee;
  white-space: pre-wrap;
}
:not(pre) > code {
  font-family: monospace;
  background-color: #eee;
  padding: 3px;
}
img {
  max-width: 100%;
  max-height: 20em;
}
img.active {
  -webkit-box-shadow: 0 0 0 2px blue;
          box-shadow: 0 0 0 2px blue;
}
blockquote {
  border-left: 2px solid #ddd;
  margin-left: 0;
  margin-right: 0;
  padding-left: 10px;
  color: #aaa;
  font-style: italic;
}
blockquote[dir="rtl"] {
  border-left: none;
  padding-left: 0;
  padding-right: 10px;
  border-right: 2px solid #ddd;
}
table {
  border-collapse: collapse;
}
td {
  padding: 10px;
  border: 2px solid #ddd;
}
input {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: .85em;
  width: 100%;
  padding: .2em;
  border: 2px solid #ddd;
  background: #fafafa;
}
input:focus {
  outline: 0;
  border-color: #ccc;
}
/**
 * Icons.
 */
.material-icons {
  font-size: 18px;
  vertical-align: text-bottom;
}
/**
 * App.
 */
.nav {
  padding: 10px 15px;
  color: #AAA;
  background: #000;
}
.nav-title {
  margin-right: 0.5em;
}
.nav-links {
  float: right;
}
.nav-link {
  margin-left: 1em;
  color: #AAA;
  text-decoration: none;
}
.nav-link:hover {
  color: #FFF;
  text-decoration: underline;
}
.tabs {
  padding: 15px 15px;
  background-color: #222;
  text-align: center;
  margin-bottom: 30px;
}
.tab {
  color: #777;
  display: inline-block;
  text-decoration: none;
  padding: 0.2em 0.5em;
  border-radius: 0.2em;
  margin-bottom: 0.2em;
}
.tab:hover {
  background: #333;
}
.tab + .tab {
  margin-left: 0.5em;
}
.tab.active {
  color: white;
  background: #333;
}
/**
 * Example.
 */
.example,
.error {
  max-width: 42em;
  margin: 0 auto 20px;
  padding: 20px;
}
.example {
  background: #fff;
}
.error {
  background: #fffae0;
}
.error .info {
  background: #fbf1bd;
  white-space: pre;
  overflow-x: scroll;
  margin-bottom: 0;
}
.menu > * {
  display: inline-block;
}
.menu > * + * {
  margin-left: 15px;
}
.button {
  color: #ccc;
  cursor: pointer;
}
.button[data-active="true"] {
  color: black;
}
.toolbar-menu {
  position: relative;
  border-bottom: 2px solid #eee;
  width: 100%;
  /* background: white; */
  margin: auto;
  padding: 0 calc(10% + 20px);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: 48px;
}
.toolbar-menu .search {
  position: absolute;
  right: calc(10% + 20px);
  margin-top: 0;
}
.toolbar-menu .search-icon {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  left: 0.5em;
  color: #ccc;
}
.toolbar-menu .search-box {
  padding-left: 2em;
  width: 100%;
}
.toolbar-menu .search-box:active {
  border-color: #ccc;
}
.toolbar-menu span {
  margin-top: 0;
}
.hover-menu {
  padding: 8px 7px 6px;
  position: absolute;
  z-index: 1;
  top: -10000px;
  left: -10000px;
  margin-top: -6px;
  opacity: 0;
  background-color: #222;
  border-radius: 4px;
  -webkit-transition: opacity .75s;
  -o-transition: opacity .75s;
  transition: opacity .75s;
}
.hover-menu .button {
  color: #aaa;
}
.hover-menu .button[data-active="true"] {
  color: #fff;
}
.emoji.selected {
  outline: 2px solid blue;
}
.check-list-item + .check-list-item {
  margin-top: 0;
}
.check-list-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
}
.check-list-item.checked {
  opacity: 0.666;
  text-decoration: line-through;
}
.check-list-item > span:first-child {
  margin-right: 0.75em;
}
.check-list-item > span:last-child {
  -ms-flex: 1 1;
      flex: 1 1;
}
.check-list-item > span:last-child:focus {
  outline: none;
}
.word-counter {
  margin-top: 10px;
  padding: 12px;
  background-color: #EBEBEB;
  display: inline-block;
}
ol,
ul {
  margin: 0;
}
li:first-child {
  margin-top: 16px;
}
li:last-child {
  margin-bottom: 16px;
}



