.spark-tabs {
  overflow: hidden;
}
.spark-tabs .spark-tabs-scroll {
  border-bottom: 1px solid #e8e8e8;
  overflow: hidden;
}
.spark-tabs .spark-tabs-scroll .spark-tabs-tab,
.spark-tabs .spark-tabs-scroll .spark-tabs-tabbar {
  display: inline-block;
  position: relative;
}
.spark-tabs .spark-tabs-scroll .spark-tabs-tab {
  padding: 12px 16px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-transition: color 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  -o-transition: color 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: color 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.spark-tabs .spark-tabs-scroll .spark-tabs-ink-bar {
  bottom: 0;
  position: absolute;
  display: block;
  background: #1890ff;
  height: 2px;
  transition: transform 0.3s cubic-bezier(0.645, 0.045, 0.355, 1), width 0.3s cubic-bezier(0.645, 0.045, 0.355, 1), -webkit-transform 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.spark-tabs .spark-tabs-content {
  -webkit-transition: margin-left 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  -o-transition: margin-left 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: margin-left 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-direction: row;
      flex-direction: row;
  will-change: margin-left;
  margin-left: 0%;
}
.spark-tabs .spark-tabs-content .spark-tabpane {
  opacity: 0;
  width: 100%;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -webkit-transition: opacity .15s;
  -o-transition: opacity .15s;
  transition: opacity .15s;
}
.spark-tabs .spark-tabs-content .spark-tabpane:active {
  opacity: 1;
}



